"use client";

import { useState } from "react";
import { Button, Space, Dropdown } from "antd";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTable,
  faBook,
  faCode,
  faBars,
  faTimes,
  faCaretDown,
} from "@fortawesome/free-solid-svg-icons";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { trackLinkClick } from "@/utils/analytics";
import { UI_STRINGS } from "@/constants/strings/ui";
import { useIsMobile } from "@/hooks/useIsMobile";

export default function Header() {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const pathname = usePathname();
  const isMobile = useIsMobile();

  const handleNavClick = (linkName: string, linkPath: string) => {
    trackLinkClick(linkName, linkPath);
    setMobileMenuOpen(false);
  };

  const isActive = (path: string) => {
    return pathname === path;
  };

  const navItems = [
    {
      name: "Documentation",
      path: "/docs",
      icon: faBook,
      children: [
        { name: "Installation", path: "/docs/installation" },
        { name: "Quick Start", path: "/docs/quick-start" },
        { name: "Column Properties", path: "/docs/column-properties" },
        { name: "Cell Editing", path: "/docs/cell-editing" },
        { name: "Themes", path: "/docs/themes" },
      ],
    },
    {
      name: "Examples",
      path: "/examples",
      icon: faCode,
      children: [
        { name: "Finance", path: "/examples/finance" },
        { name: "HR", path: "/examples/hr" },
        { name: "Manufacturing", path: "/examples/manufacturing" },
        { name: "Billing", path: "/examples/billing" },
      ],
    },
  ];

  return (
    <header className="bg-white shadow-sm sticky top-0 z-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between">
          <div className="flex items-center">
            <Link
              href="/"
              onClick={() => handleNavClick("Logo", "/")}
              className="flex items-center space-x-2"
            >
              <FontAwesomeIcon icon={faTable} className="text-blue-600 text-2xl" />
              <span className="text-xl font-bold text-gray-800">Simple Table</span>
            </Link>
          </div>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex items-center space-x-8">
            {navItems.map((item) => (
              <Dropdown
                key={item.name}
                menu={{
                  items: item.children.map((child) => ({
                    key: child.path,
                    label: (
                      <Link
                        href={child.path}
                        onClick={() => handleNavClick(child.name, child.path)}
                        className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                      >
                        {child.name}
                      </Link>
                    ),
                  })),
                }}
                placement="bottomLeft"
              >
                <Button
                  type="text"
                  className={`flex items-center space-x-1 ${
                    isActive(item.path) ? "text-blue-600" : "text-gray-600"
                  }`}
                >
                  <FontAwesomeIcon icon={item.icon} />
                  <span>{item.name}</span>
                  <FontAwesomeIcon icon={faCaretDown} className="ml-1" />
                </Button>
              </Dropdown>
            ))}
          </nav>

          {/* Mobile menu button */}
          <div className="md:hidden">
            <Button
              type="text"
              onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
              className="text-gray-600"
            >
              <FontAwesomeIcon icon={mobileMenuOpen ? faTimes : faBars} />
            </Button>
          </div>
        </div>

        {/* Mobile Navigation */}
        {mobileMenuOpen && (
          <div className="md:hidden py-4">
            <nav className="flex flex-col space-y-4">
              {navItems.map((item) => (
                <div key={item.name}>
                  <Button
                    type="text"
                    className={`flex items-center space-x-2 w-full ${
                      isActive(item.path) ? "text-blue-600" : "text-gray-600"
                    }`}
                  >
                    <FontAwesomeIcon icon={item.icon} />
                    <span>{item.name}</span>
                  </Button>
                  <div className="pl-4 mt-2 space-y-2">
                    {item.children.map((child) => (
                      <Link
                        key={child.path}
                        href={child.path}
                        onClick={() => handleNavClick(child.name, child.path)}
                        className={`block py-2 ${
                          isActive(child.path) ? "text-blue-600" : "text-gray-600"
                        }`}
                      >
                        {child.name}
                      </Link>
                    ))}
                  </div>
                </div>
              ))}
            </nav>
          </div>
        )}
      </div>
    </header>
  );
}
